/**
 * Created by gaolei on 2017/4/21.
 *
 * 每日站点巡查明细报表-日
 */
import React from 'react';
import {connect} from 'react-redux';
import CenterTopNav from '../../components/public/centerTopNav';
import {browserHistory } from 'react-router';
import Pagination from '../../components/public/pagination';
import {dialogHandle} from '../../actions/dialog';
import SelectBox from '../../components/public/selectBox';
import ReportFormTable from '../../components/public/reportFormTable';
import DepartmentTree from '../../components/public/department';
import {reportFormCommonHandle,queryProvince,queryCity,queryCounty,getBankTypeCodes,getCompanyByCompanyType} from '../../actions/reportForm/public/common';
import {siteDetailsDataDayHandle,siteDetailsDataDayLoadData,getInspectFeedback,siteDetailsDataDayExport} from '../../actions/reportForm/siteDetailsDataDay';
import Regional from '../../components/reportForm/regional';
import ExportButton from '../../components/reportForm/exportButton';

class siteDetailsDataDayMain extends React.Component {

	componentDidMount(){
		this.resetFun(true);
        var _that = this;
        $(function() {        	
            $( "#datepicker" ).datepicker({
            	dateFormat: 'yy-mm-dd',
            	maxDate:-1,
            	changeYear: true,          // 年下拉菜单
                changeMonth: true,             // 月下拉菜单
            	onSelect: function (date) {
            		_that.props._siteDetailsDataDayHandle({
			            reportDate: date
			        });
	            }
            });
        });
        
		this.props._queryProvince({
            reportName:"modeinspectdetailDaily"
        });
        this.props._getBankTypeCodes({
            reportName:"modeinspectdetailDaily"
        });
        
        let titleList=this.props.siteDetailsDataDay.tableTitle;
        titleList[titleList.length-1].htmlType[0].callBack=this.dataInfoFun.bind(this);
        
        this.props._siteDetailsDataDayHandle({
            tableTitle:titleList
        });
        
        this.props._getCompanyByCompanyType();
        
    }
	//加载分页插件
    loadPage(options) {
        this.props._siteDetailsDataDayHandle({
            loadPageFlag: false
        });
        this.pageNavClick(1,10,options);
    }
	//调用分页加载
    pageNavClick(n,pageSize,options) {
        this.props._siteDetailsDataDayHandle({
            curPage: n,
            pageLimit:pageSize
        });
        this.loadData(n,pageSize,options?options:this.props.siteDetailsDataDay.temp_options);
    }
    //加载数据
    loadData(n,pageSize,options){
    	//加载中
        this.props._siteDetailsDataDayHandle({
            curPage: n,
            pageLimit:pageSize
        });   
        
        let ajaxData = {
        	curPage: n,//当前页
            pageSize: pageSize,//每页多少行
            reportDate: options.reportDate,//数据日期
            provinceName:options.province.name=="全部"?"":options.province.name,//省
			cityName:options.city.name=="全部"?"":options.city.name,//市
			countyName:options.county.name=="全部"?"":options.county.name,//区县
			fullTreePath:options.department.val,//部门
            bankTypeCode: options.bank=='全部'?'':options.bank,//银行code
            nodeName: options.siteName,//站点啊名称
            nodeCode: options.siteNum,//站点编号
            participant: options.partakePeople,//参与人员
            inspectjobStatus: options.taskState,//任务状态
        };
    	this.props._siteDetailsDataDayLoadData({
    		data:ajaxData
       });        
    }
    
    
    selectClick(id,val,name){
        switch(id){
            case 1 :
               this.props._siteDetailsDataDayHandle({
                   province:{val:val,name:name},
                   city:{val:'',name:''},//选中的城市
                   county:{val:'',name:''}//选中的县
               })

               if(val==''){
                  //用户选择全部的情况

                   //重置市县区
                   this.props._reportFormCommonHandle({
                       cityList:[],
                       countyList:[]
                   })
               }else{
                   //重置县区
                   this.props._reportFormCommonHandle({
                       countyList:[]
                   })
                   //更新城市数据
                   this.props._queryCity({
                       provinceCode:val,
                       reportName:"modeinspectdetailDaily"
                   })
               }

               break;
            case 2 :
                this.props._siteDetailsDataDayHandle({
                    city:{val:val,name:name},
                    county:{val:'',name:''}//选中的县
                })
                if(val==''){
                    //用户选择全部的情况

                    //重置县区
                    this.props._reportFormCommonHandle({
                        countyList:[]
                    })
                }else{
                    //更新县区数据
                    this.props._queryCounty({
                        cityCode:val,
                        reportName:"modeinspectdetailDaily"
                    })
                }

                break;
            case 3 :
                this.props._siteDetailsDataDayHandle({
                    county:{val:val,name:name}
                })
                break;
            case 4 :
                this.props._siteDetailsDataDayHandle({
                    bank:val
                })
                break;
            default :
                break;
        }
    }
    showProvince(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.provinceList.map((val,key)=>{
             temp_arr.push({value:val.districtCode,name:val.districtName})
        });
        return arr.concat(temp_arr);
    }

    showCity(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.cityList.map((val,key)=>{
            temp_arr.push({value:val.districtCode,name:val.cityName})
        });
        return arr.concat(temp_arr);
    }

    showCounty(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.countyList.map((val,key)=>{
            temp_arr.push({value:val.districtCode,name:val.countyName})
        });
        return arr.concat(temp_arr);
    }

    showBank(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.bankList.map((val,key)=>{
            temp_arr.push({value:val.bankTypeCode,name:val.bankTypeName})
        });
        return arr.concat(temp_arr);
    }
    //部门
    departmentReset(value,name){
    	this.props._siteDetailsDataDayHandle({
			department:{val:value,name:name}
		});
    }
    //站点名称
    siteNameFun(event){
    	this.props._siteDetailsDataDayHandle({
			siteName:event.target.value
		});
    }
    //站点编号
    siteNumFun(event){    	
    	this.props._siteDetailsDataDayHandle({
			siteNum:event.target.value
		});
    }
    //参与人员
    partakePeopleFun(event){
    	this.props._siteDetailsDataDayHandle({
			partakePeople:event.target.value
		});
    }
    //任务状态
    taskStateFun(value,name){
    	this.props._siteDetailsDataDayHandle({
			taskState:value
		});
    }
    
	
	//查询
	queryData(){
		if(this.props.siteDetailsDataDay.tbodyList=='loading'){
			this.props._dialogHandle({
                type: "tips",
                time: 2000,
                content: "正在查询中，请耐心等待！",
                tipsType: "warning",
                show: true
            })
		}else{
			let siteNumCheck=$.XlCheck({
				val:this.props.siteDetailsDataDay.siteNum,
				rule:["Number"] 
			});
			if(!siteNumCheck.Number){
				this.props._dialogHandle({
					show:true,
					content:'站点编号只能输入数字',
					type:"tips",
					tipsType:"warning"
				});
				return false;
			}
			
			let options = {
				reportDate:this.props.siteDetailsDataDay.reportDate,//数据日期
		        province:this.props.siteDetailsDataDay.province,//选中的省
			    city:this.props.siteDetailsDataDay.city,//选中的城市
			    county:this.props.siteDetailsDataDay.county,//选中的县
		        department:this.props.siteDetailsDataDay.department,//部门code
		        bank:this.props.siteDetailsDataDay.bank,//银行code
		        siteName:this.props.siteDetailsDataDay.siteName,//站点名称
		        siteNum:this.props.siteDetailsDataDay.siteNum,//站点编号
		        partakePeople:this.props.siteDetailsDataDay.partakePeople,//参与人员
		        taskState:this.props.siteDetailsDataDay.taskState,//任务状态
			};
			this.props._siteDetailsDataDayHandle({
				tbodyList: 'loading',
				temp_options:options
			});
			this.loadData(1,this.props.siteAddDataMonth.pageLimit,options);
		}
		
	}
	//重置
	resetFun(bool){
		if(bool===true){
           //初次进入页面，所有参数初始化
           let options={
                reportDate: moment().subtract(1,"days").format('YYYY-MM-DD'),//数据日期
	            province:{val:"",name:""},//省
				city:{val:"",name:""},//市
				county:{val:"",name:""},//区县
	            department:{val:"",name:""},//部门            
	            bank: "",//银行code
	            siteName: "",//站点名称
	            siteNum: "",//站点编号
	            partakePeople: "",//参与人员
	            taskState: "",//任务状态
          };
           this.props._siteDetailsDataDayHandle({
                reportDate: moment().subtract(1,"days").format('YYYY-MM-DD'),//数据日期
	            province:{val:"",name:""},//省
				city:{val:"",name:""},//市
				county:{val:"",name:""},//区县
	            department:{val:"",name:""},//部门            
	            bank: "",//银行code
	            siteName: "",//站点名称
	            siteNum: "",//站点编号
	            partakePeople: "",//参与人员
	            taskState: "",//任务状态
                temp_options:options
           });
           //进入页面发起默认请求加载数据
           this.loadPage(options);
		}else{
			this.props._siteDetailsDataDayHandle({
	            reportDate: moment().subtract(1,"days").format('YYYY-MM-DD'),//数据日期
	            province:{val:"",name:""},//省
				city:{val:"",name:""},//市
				county:{val:"",name:""},//区县
	            department:{val:"",name:""},//部门            
	            bank: "",//银行code
	            siteName: "",//站点名称
	            siteNum: "",//站点编号
	            partakePeople: "",//参与人员
	            taskState: "",//任务状态
	        });
		}
		this.selectClick(1,'','');
		
	}
	//导出报表
    exportExcel(){
        let options=this.props.siteDetailsDataDay.temp_options;
        this.props._siteDetailsDataDayExport({
            reportDate: options.reportDate,//数据日期
            provinceName:options.province.name=="全部"?"":options.province.name,//省
			cityName:options.city.name=="全部"?"":options.city.name,//市
			countyName:options.county.name=="全部"?"":options.county.name,//区县
			fullTreePath:options.department.val,//部门
            bankTypeCode: options.bank=='全部'?'':options.bank,//银行code
            nodeName: options.siteName,//站点啊名称
            nodeCode: options.siteNum,//站点编号
            participant: options.partakePeople,//参与人员
            inspectjobStatus: options.taskState,//任务状态
        });
    }
	
	//查看详情
	dataInfoFun(id){
		let _this = this;
		this.props._getInspectFeedback({
			id:id,
			callBack:(data)=>{
				_this.feedbackInfo(data);
			}
		});
	}
	//反馈详情
	feedbackInfo(data){
		let html =(
			<ul className="tcc-patrol-feedback">
				<li>
					<b className="text-r">设备:</b>
					<span>{data.EQUIPMENT_ISOK == ""?"暂无问题":data.EQUIPMENT_ISOK}</span>
				</li>
				<li>
					<b className="text-r">安全:</b>
					<span>{data.SAFETY_ISOK == ""?"暂无问题":data.SAFETY_ISOK}</span>
				</li>
				<li>
					<b className="text-r">操作:</b>
					<span>{data.TRAIN_ISOK == ""?"暂无问题":data.TRAIN_ISOK}</span>
				</li>
				<li>
					<b className="text-r">业绩:</b>
					<span>{data.PERFORMANCE_ISOK == ""?"暂无问题":data.PERFORMANCE_ISOK}</span>
				</li>
				<li>
					<b className="text-r">话术:</b>
					<span>{data.CONVERSATION_ISOK == ""?"暂无问题":data.CONVERSATION_ISOK}</span>
				</li>
				<li>
					<b className="text-r">信息录入:</b>
					<span>{data.SYSTEM_ISOK == ""?"暂无问题":data.SYSTEM_ISOK}</span>
				</li>
				<li>
					<b className="text-r">监控:</b>
					<span>{data.MONITOR_ISOK == ""?"暂无问题":data.MONITOR_ISOK}</span>
				</li>
				<li>
					<b className="text-r">广告标识:</b>
					<span>{data.LAYOUT_ISOK == ""?"暂无问题":data.LAYOUT_ISOK}</span>
				</li>
				<li>
					<b className="text-r">大额交易:</b>
					<span>{data.PAYMENT_ISOK == ""?"暂无问题":data.PAYMENT_ISOK}</span>
				</li>
				<li>
					<b className="text-r">风险交易:</b>
					<span>{data.IOU_ISOK == ""?"暂无问题":data.IOU_ISOK}</span>
				</li>
				<li>
					<b className="text-r">代理业务:</b>
					<span>{data.FINANCE_ISOK == ""?"暂无问题":data.FINANCE_ISOK}</span>
				</li>
				<li>
					<b className="text-r">问题建议:</b>
					<span>{data.ADVICE_ISOK == ""?"暂无问题":data.ADVICE_ISOK}</span>
				</li>
				<li>
					<b className="text-r">其他:</b>
					<span>{data.PROBLEM_ISOK == ""?"暂无问题":data.PROBLEM_ISOK}</span>
				</li>
			</ul>
			
		);
		this.props._dialogHandle({
            show:true,
            title:"巡查反馈",
            type:"confirm",
            width:'800px',
            height:'500px',
            successBtn:false,//是否显示确定按钮  默认为显示
            failText:"关闭",//确定按钮文字
            content:html,
            success:function(){
            }
       });
	}
	



    render() {
        return (
            <div className="height100p">
                <CenterTopNav title="每日站点巡查明细报表" parentList={[{name:"数据报表"},{name:"巡查日报"}]} />
                <div className="height100pY plr26">
					{/*form content begin*/}
					<div className="pub-form-top clearfix">
						<ul>
							<li>
								<span>数据日期:</span>
								<input id="datepicker" type="text" value={this.props.siteDetailsDataDay.reportDate} readOnly />
							</li>
                           <Regional
                                selectClick={this.selectClick.bind(this)}
                                provinceVal={this.props.siteDetailsDataDay.province.val}
                                cityVal={this.props.siteDetailsDataDay.city.val}
                                countyVal={this.props.siteDetailsDataDay.county.val}
                                provinceList={this.showProvince()}
                                cityList={this.showCity()}
                                countyList={this.showCounty()}
                            />
						</ul>
						<ul>
							<li>
								<span>所属部门:</span>
								<DepartmentTree value={this.props.siteDetailsDataDay.department.name}
	                                departmentData={this.props.reportFormCommon.companyList}
	                                callBack={this.departmentReset.bind(this)}
	                            />
							</li>
							<li>
                                <span>合作银行:</span>
                                <SelectBox
                                    callBack={this.selectClick.bind(this,4)}
                                    value={this.props.siteDetailsDataDay.bank}
                                    list={this.showBank()}
                                />
                            </li>
							<li>
								<span>站点名称:</span>
								<input type="text" placeholder="请输入站点名称" onChange={this.siteNameFun.bind(this)} value={this.props.siteDetailsDataDay.siteName} />
							</li>
							<li>
								<span>站点编号:</span>
								<input type="text" placeholder="请输入站点编号" onChange={this.siteNumFun.bind(this)}  value={this.props.siteDetailsDataDay.siteNum}  />
							</li>
						</ul>
						<ul>
							<li>
								<span>参与人员:</span>
								<input type="text" placeholder="请输入参与人员" onChange={this.partakePeopleFun.bind(this)} value={this.props.siteDetailsDataDay.partakePeople} />
							</li>							
							<li>
								<span>任务状态:</span>
								<SelectBox
                                   value={this.props.siteDetailsDataDay.taskState}
                                   list={[{value:'',name:'全部'}, {value:'待完成',name:'待完成'},{value:'已关闭',name:'已关闭'},{value:'已完成',name:'已完成'}]} 
                                   callBack={this.taskStateFun.bind(this)}
                                />
							</li>
						</ul>
						
						<div className="pub-form-btns clearfix">
							<div className="left">
								<button onClick={this.queryData.bind(this)}>查询</button>
								<button onClick={this.resetFun.bind(this)}>重置</button>
							</div>
							<ExportButton
                               data={this.props.login.Jurisdiction.ReportForm.btns.siteDetailsDataDay}
                               clickBack={this.exportExcel.bind(this)}
                            />							
						</div>
						
					</div>
					{/*form content end*/}
					
					{/*content begin*/}
					<div className="form-content-wrap">
						<div className="form-table-wrap">
						   <ReportFormTable
						   	 width="130%" 
						     dataList={this.props.siteDetailsDataDay.tableList}
							 titleList={this.props.siteDetailsDataDay.tableTitle}
							 colspan={14}
							 status={this.props.siteDetailsDataDay.tbodyList}
						   />		
							
						</div>
						<Pagination
	                        curPage={this.props.siteDetailsDataDay.curPage}
	                        totalNumber={this.props.siteDetailsDataDay.totalSize}
	                        pageLimt={this.props.siteDetailsDataDay.pageLimit}
	                        pageClick={this.pageNavClick.bind(this)}
	                    />
						
					</div>
					{/*content end*/}
					
                </div>
            </div>
        )
    }
}

function mapStateToProps(state){
    return state;
}
function mapDispatchToProps(dispatch){
    return { 
    	_siteDetailsDataDayHandle:(options)=>{
            dispatch(siteDetailsDataDayHandle(options));
        },
        _siteDetailsDataDayLoadData:(options)=>{
       		dispatch(siteDetailsDataDayLoadData(options))
        },
        _queryProvince:(options)=>{
            dispatch(queryProvince(options));
        },
        _queryCity:(options)=>{
            dispatch(queryCity(options));
        },
        _queryCounty:(options)=>{
            dispatch(queryCounty(options));
        },
        _getBankTypeCodes:(options)=>{
        	dispatch(getBankTypeCodes(options))
        },
        _getCompanyByCompanyType:(options)=>{
        	dispatch(getCompanyByCompanyType(options))
        },
        _reportFormCommonHandle:(options)=>{
        	dispatch(reportFormCommonHandle(options))
        },
        _dialogHandle:(options)=>{
        	dispatch(dialogHandle(options))
        },
        _getInspectFeedback:(options)=>{
        	dispatch(getInspectFeedback(options))
        },
        _siteDetailsDataDayExport:(options)=>{
        	dispatch(siteDetailsDataDayExport(options))
        },
        
    }
}
const siteDetailsDataDay = connect(
	mapStateToProps,
    mapDispatchToProps
)(siteDetailsDataDayMain);

export default  siteDetailsDataDay;